import { SendShot } from './api/BattleAPI'
import { getString } from '../Translation/String'

export const togglePopup = (a: boolean, classColor: string = "", message: string = "") => {
  let popup = document.getElementById("popup")
  if(popup!==null) {
    popup.classList.remove("success", "error", "info", "warn")
    if(a===true) {
      popup.classList.add("show")
      popup.classList.add(classColor)
    } else {
      popup.classList.remove("show")
    }
    popup.innerHTML = message + '<span>Ã—</span>'
  }
}

export const FieldInit = (): number[][] => {
  // return array filled with false value
  let ships = []
  for(let i=0; i<10; i++) {
    let ship = []
    for(let j=0; j<10; j++) {
      ship.push(0)
    }
    ships.push(ship)
  }
  return ships
}

export const HideOrNot = (a: number): any => {
  if(a===1) {
    return { display: 'none' }
  }
}

export const BoolToOnOff = (a: boolean): string => {
  if(a===true) {
    return getString('enabled')
  } else {
    return getString('disabled')
  }
}

enum ShotResult {none, hit, miss}

export const HitOrMiss = async (id: string,
                                x: number,
                                y: number,
                                changeField: (arg0: number, arg1: number, arg2: number) => void) => {
  let type = 'none'
  async function returnResponse(response: any) {
    type = response.type
  }
  await SendShot(id, x, y, returnResponse)

  if (type===ShotResult[ShotResult.miss]) {
    changeField(x, y, 1)
  } else if (type === ShotResult[ShotResult.hit]) {
    changeField(x, y, 2)
  }
}

export const removeYID = (): void => {
  let yID = document.getElementById('yID')
  if(yID!==null) {
    yID.style.display = 'none'
  }
}

export const delay = (ms: number) => {
  return new Promise( resolve => setTimeout(resolve, ms) );
}

type ship = {
  res: string,
  type: string,
  index: number,
  beg: number,
  end: number,
  len: number
}
/*
  "res": success, fail or no need (already find)
  "type": row or col
  "index": row (col) index
  "beg": begin ship index
  "end": end index
  "len": ship length
*/

export type AllShips = {
  result: string,
  ships: any
}

export const validateAndTransform = (field: number[][]): AllShips => {
  let ships:ship[] = []
  let result = {result: 'fail', ships: ships}
  let tmp: ship

  let i: number
  let j: number
  for(i = 0; i < 10; i++) {
    for(j = 0; j < 10; j++) {

      if(field[i][j] === 1) {
        tmp = findShip(field, i, j)
        if(tmp.res === 'fail') {
          return result
        } else if(tmp.res === 'success') {
          ships.push(tmp)
        }
      }

    }
  }
  convertShips(ships)
  result.result = 'success'
  result.ships = ships
  return result
}

const findShip = (field: number[][], i: number, j: number): ship => {
  let result:ship = {res: 'none', type: 'none', index: -1, beg: -1, end: -1, len: -1}
  // not side and not begin ship
  if(i !== 0 && field[i - 1][j] === 1) {
    result.res = 'finded'
    return result
  }
  if(j !== 0 && field[i][j - 1] === 1) {
    result.res = 'finded'
    return result
  }

  let beg = i
  let end = i

  /**
   * Ð¿Ñ€Ð¾Ð²ÐµÑ€ÐºÐ° Ð½Ð° Ð½Ð°Ð»Ð¸Ñ‡Ð¸Ðµ Ð·Ð°Ð½ÑÑ‚Ñ‹Ñ… ÐºÐ»ÐµÑ‚Ð¾Ðº Ð² "Ð¾Ñ€ÐµÐ¾Ð»Ðµ" ÐºÐ¾Ñ€Ð°Ð±Ð»Ñ - Ð²Ð²ÐµÑ€Ñ…/Ð²Ð½Ð¸Ð·, Ð²Ð¿Ñ€Ð°Ð²Ð¾/Ð²Ð»ÐµÐ²Ð¾ Ð¾Ñ‚ Ð¾ÑÐ½Ð¾Ð²Ð½Ð¾Ð¹ Ð¾ÑÐ¸
   * @param param {"ÐºÐ¾Ð¾Ñ€Ð´Ð¸Ð½Ð°Ñ‚Ð°" Ð¿Ð¾ ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ð¹ Ñ€Ð°ÑÐ¿Ð¾Ð»Ð¾Ð¶ÐµÐ½ ÐºÐ¾Ñ€Ð°Ð±Ð»ÑŒ - i, j}
   * @param x {1 ÐºÐ¾Ð¾Ñ€Ð´Ð¸Ð½Ð°Ñ‚Ð° Ñ‚Ð¾Ñ‡ÐºÐ¸}
   * @param y {2 ÐºÐ¾Ð¾Ñ€Ð´Ð¸Ð½Ð°Ñ‚Ð° Ñ‚Ð¾Ñ‡ÐºÐ¸}
   * @param a {Ð¾Ñ‚ÐºÐ»Ð¾Ð½ÑÑ‚ÑŒÑÑ (Ð½Ð° ÑÐºÐ¾Ð»ÑŒÐºÐ¾, Ð½Ð¾ ÑÑ‚Ð°Ð²Ð¸Ð¼ 0 Ð¸Ð»Ð¸ 1) Ð² Ñ‚ÐµÐºÑƒÑ‰ÐµÐ¹ Ð¿Ð¾Ð·Ð¸Ñ†Ð¸Ð¸ Ð² ÑÑ‚Ð¾Ñ€Ð¾Ð½Ñƒ Ð¸Ð»Ð¸ Ð½ÐµÑ‚ - Ð´Ð»Ñ x}
   * @param b {Ð°Ð½Ð°Ð»Ð¾Ð³Ð¸Ñ‡Ð½Ð¾ - Ð´Ð»Ñ y}
   */
  let check = (param: number, x: number, y: number, a: number, b: number): boolean => {
    // ship on side
    if(param === 0 && field[x + a][y + b] === 1) {
      return false
    }
    if(param === 9 && field[x - a][y - b] === 1) {
      return false
    }
    // ship not near side
    if((param > 0 && param < 9) &&
      (field[x - a][y - b] === 1 || field[x + a][y + b] === 1))
    {
      return false
    }
    return true
  }

  // vertical ship and len > 1
  if(i < 9 && field[i + 1][j] === 1) {
    while(end < 10) {
      /*
        checking smth like
        0000
        0110
        0010
        0000
       */
      if(check(j, end, j, 0, 1) === false) {
        result.res = 'fail'
        return result
      }
      // SUCCESS
      // find zero or field side
      if(end === 9 || field[end + 1][j] === 0) {
        /*
          check smth like
          010
          010
          100
         */
        if(end !== 9 && check(j, end + 1, j, 0, 1) === false) {
          result.res = 'fail'
          return result
        }
        result = {
          res: 'success',
          type: 'col',
          index: j,
          beg: beg,
          end: end,
          len: end - beg + 1
        }
        return result
      }
      end++
    }
  }
  // horizontal ship and len > 1
  else if(j < 9 && field[i][j + 1] === 1) {
    beg = end = j
    while(end < 10) {
      if(check(i, i, end, 1, 0) === false) {
        result.res = 'fail'
        return result
      }
      // find zero or field side
      if(end === 9 || field[i][end + 1] === 0) {
        if(end !== 9 && check(i, i, end + 1, 1, 0) === false) {
          result.res = 'fail'
          return result
        }
        result = {
          res: 'success',
          type: 'row',
          index: i,
          beg: beg,
          end: end,
          len: end - beg + 1
        }
        return result
      }
      end++
    }
  }
  // len == 1
  else {
    if(j < 9 && check(i, i, j + 1, 1, 0) === false) {
      result.res = 'fail'
      return result
    }
    // index == i because type == row
    result = {
      res: 'success',
      type: 'row',
      index: i,
      beg: beg,
      end: end,
      len: end - beg + 1
    }
    return result
  }
  return result
}

